import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    searchString : "",
    isSearchPropertiesLoading: false,
    searchResultsList: [],
    searchPropertiesErrorMessage: ""
}

const propertiesSearchSlice = createSlice({
    name: "propertiesSearch",
    initialState,
    reducers:{
        setSearchString: (state, action)=>{
            state.searchString = action.payload;

        },
        setIsSearchPropertiesLoading: (state, action)=>{
            state.isSearchPropertiesLoading = action.payload;
        },
        setSearchResultsList: (state, action)=>{
            state.searchResultsList = action.payload;
        },
        setSearchPropertiesErrorMessage: (state, action)=>{
            state.searchPropertiesErrorMessage = action.payload;
        }
    }
});

export const {setSearchString, setIsSearchPropertiesLoading,setSearchResultsList, setSearchPropertiesErrorMessage} =  propertiesSearchSlice.actions;
export default propertiesSearchSlice.reducer;

